        >Just Log me out</a>
        <button class="btn btn-primary px-2 d-flex align-items-center" @click="releaseLogout">
         <span v-if="loggingOut">Releasing</span>
         <span v-else>Releae & LogOut</span>
          <div v-if="loggingOut" class="spinner-border text-light ml-2" style="width:1.5rem;height:1.5rem;" role="status">
