<script>
import Vue from "vue";
import DashboardIcon from "@/icons/Dashboard";
// import VueTippy, { TippyComponent } from "vue-tippy";
import "line-awesome/src/line-awesome.scss";
import { mapGetters } from "vuex";
import axios from "axios";
// Vue.use(VueTippy);
// Vue.component("tippy", TippyComponent);
export default {
  components: {
    DashboardIcon
  },
  data() {
    return {
      loggingOut:false,
      upperRoute: [
        {
          path: "/dashboard",
          tooltip: "Dashboard",
          icon: "las la-chalkboard ",
          role: "Admin,Team"
        },
        {
          path: "/conversations",
          icon: "las la-comments ",
          tooltip: "Conversations"
        }
      ],
      lowerRoute: [
        {
          path: "/settings",
          icon: "las la-cog ",
          tooltip: "Settings",
          role: "Admin,Team"
        },
        {
          path: "/logout",
          icon: "la la-power-off ",
          tooltip: "Log Out"
        }
      ]
    };
  },
  computed: {
    ...mapGetters(["admin"]),
    channel() {
      return this.$store.state.channel;
    },
    upperRouteRoleFiltered() {
      return this.upperRoute.filter(x => {
        if (x.role) {
          return x.role.includes(this.admin.role);
        }
        return true;
      });
    },
    lowerRouteRoleFiltered() {
      return this.lowerRoute.filter(x => {
        if (x.role) {
          return x.role.includes(this.admin.role);
        }
        return true;
      });
    }
    //...mapGetters(["admin"])
  },
  methods: {
    checkCurrentRoute(currentroute) {
      if (currentroute === this.$router.currentRoute.path) {
        //console.log(true)
        return true;
      } else {
        //console.log(false)
        return false;
      }
    },
    updateRoute(path) {
      if (path == "/logout" && this.admin.role != "Admin") {
        this.$modal.show("release");
      } else if (path == "/logout") {
        location.href = path;
      } else {
        this.$router.push(path);
      }
    },
    releaseLogout() {
      this.loggingOut=true;
      axios
        .post("release", {
          channelId: this.channel.id,
          channelName: this.channel.name
        })
        .then(res => {
          if (res.status == 200) location.href = "/logout";
        }).
        catch(e=>{
          this.loggingOut=false;
        })
    }
  }
};
</script>
